@use "../abstracts/variables";
@use "../abstracts/mixins";

.cartpage {
  width: 100%;

  @include mixins.boxFlex(center, unset, column);

  @include mixins.media("tablet") {
    margin: 0 !important;
  }
  @include mixins.media("desktop-m") {
    width: 1440px;
    margin: auto !important;
  }

  .cart-margins__inline {
    width: auto;
  }

  .cart-items__empty-wrapper {
    min-height: auto;
    border-bottom: 1px solid variables.$color-background;

    @include mixins.media("tablet") {
      border-bottom: none;
    }
  }

  .cart-items__empty {
    text-transform: uppercase;
  }

  &-header {
    margin-top: variables.$margin-xxl;
    @include mixins.boxFlex(center, center, column);

    @include mixins.media("tablet") {
      margin-top: variables.$margin-section;
      @include mixins.boxFlex(space-between, center, row);
    }

    & > &__title {
      margin-bottom: 6px;
      font-size: variables.$font-size--mobile--h1-customer;
      line-height: variables.$line-height--mobile--h1-customer;
      text-align: center;
      color: variables.$color-black;

      @include mixins.media("tablet") {
        margin-top: 0;
        margin-bottom: 0;
      }

      @include mixins.media('tablet-l') {
        font-size: variables.$font-size--mobile-h1;
        line-height: variables.$line-height--mobile-h1;
      }
    }

    &__link {
      transform: translateX(-10px);
      @include mixins.media("tablet-l") {
        transform: translateX(0);
      }
    }
  }

  &-items {
    margin-top: variables.$margin-l;

    @include mixins.media("tablet") {
      margin-top: 65px;
    }
  }

  &:has(.cart-items__empty-wrapper) {
    #container-cart-page {
      padding: 0;
      display:none;
    }
  }

  .cart-items__empty-wrapper {
    @include mixins.media("tablet") {
      padding: 56px 0 30px;
    }
  }

  #container-cart-page {
    position: relative;
    align-self: flex-end;
    width: 100%;
    max-width: 375px;
    padding: 0 0 60px;

    @include mixins.media("phone-m") {
      width: 343px;
    }

    .cart-discount {
      margin-bottom: variables.$margin-l;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &-footer {
    margin-top: variables.$margin-l;
    @include mixins.boxFlex(space-between, center, row);

    &__info {
      @include mixins.media("tablet") {
        font-size: variables.$font-size--general-up-m;
      }
    }
    &__info,
    &__total-text {
      display: inline-block;
    }
    &__total-text {
      margin: 0;
      font-family: variables.$font-family--general;
      font-weight: variables.$font-light;
      font-size: variables.$font-size--general-up-m;
      line-height: variables.$line-height--general-m;
      color: variables.$color-black;
    }
    &__info--shipping {
      display: none;
    }
    &__btn {
      width: 100%;
      position: absolute !important;
      bottom: 24px;
      left: 0;
      cursor: pointer !important;
    }
    a {
      color: inherit;
    }
  }

  #discount-validation[data-active="false"] {
    display: none;
  }

  .cart {
    &-items__empty {
      width: 85%;
      margin: 0 auto;
      padding: 42px 0 26px;
      font-size: variables.$font-size--general-up-l;
      line-height: variables.$line-height--mobile-h5;
      color: variables.$color-background-2;

      @include mixins.media("phone-m") {
        width: 100%;
      }
      @include mixins.media("tablet") {
        padding: 90px 0;
      }
    }

    &-items__container {
      grid-template-columns: 78px calc(100% - 94px);
      margin: 0;
      padding: variables.$margin-m 0;
      border-bottom: 1px solid variables.$color-background;

      &:first-of-type {
        border-top: 1px solid variables.$color-background;
      }

      @include mixins.media("desktop") {
        grid-template-columns: 120px calc(100% - 136px);
      }

      &--image {
        @include mixins.media("desktop") {
          width: 120px;
          height: 120px;
        }
      }
      &--product-title {
        @include mixins.media("desktop") {
          width: 120px;
        }
      }
      .item-price {
        @include mixins.media("tablet") {
          font-size: variables.$font-size--general-m;
        }
      }

      .item-delete {
        @include mixins.media("tablet") {
          top: 50%;
          right: 50%;
          transform: translate(10%, -50%);
          font-size: variables.$font-size--general-m;
          @include mixins.boxFlex();
        }
        @include mixins.media("desktop") {
          transform: translate(-10%, -50%);
          font-weight: variables.$font-regular;
          line-height: variables.$line-height--general-up-s;
        }

        span {
          display: none;
          margin-left: variables.$margin-s;
          text-decoration: underline;
          text-transform: uppercase;
          color: variables.$color-principal;

          @include mixins.media("tablet") {
            display: inline;
          }
        }
      }

      .item-line-price {
        display: none;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);

        @include mixins.media("tablet") {
          display: block;
          font-size: variables.$font-size--general-m;
          line-height: variables.$line-height--general-m;
        }
      }

      &--title {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: calc(100% - 70px);

        @include mixins.media("tablet") {
          max-width: 37%;
          font-size: variables.$font-size--general-m;
        }
        @include mixins.media("desktop") {
          max-width: 35%;
        }

        span {
          display: inline;
        }
      }

      &--condition {
        @include mixins.media("tablet") {
          display: none;
        }
      }
    }
  }

  .cart-upsell {
    margin: variables.$margin-section 0;

    @media screen and (max-width: 768px) {
      margin: variables.$margin-section calc(0rem - variables.$paddings-side--movil);
    }

    &-title {
      display: block;
      text-align: center;
      padding: 0;
    }

    .swiper-wrapper .swiper-slide {
      height: auto;
    }

    .without-arrows {
      .swiper-wrapper {
        justify-content: flex-start;

        @include mixins.media("tablet") {
          justify-content: center;
        }
      }

      .swiper-button {
        display: none;
      }
    }
  }

  &:has(.cart-items__empty-wrapper) {
    .cart-discount {
      display: none;
    }
  }
}
